table {
  width: 100%;
}

tr.example-detail-row {
  height: 0;
}

tr.example-element-row:not(.example-expanded-row):hover {
  background: whitesmoke;
}

tr.example-element-row:not(.example-expanded-row):active {
  background: #efefef;
}

.example-element-row td {
  border-bottom-width: 0;
}

.example-element-detail {
  overflow: hidden;
  flex: 0 0 100%;
}

.example-element-diagram {
  min-width: 80px;
  border: 2px solid black;
  padding: 8px;
  font-weight: lighter;
  margin: 8px 0;
  height: 104px;
}

.example-element-symbol {
  font-weight: bold;
  font-size: 40px;
  line-height: normal;
}

.example-element-description {
  padding: 16px;
}

.example-element-description-attribution {
  opacity: 0.5;
}

.loader {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  background: linear-gradient(0deg, rgba(255, 61, 0, 0.2) 33%, #ff3d00 100%);
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 145px;
  height: 145px;
  border-radius: 50%;
  background: #fff;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

#logo-inside-loader {
  position: absolute;
  top: 65px;
  left: 55px;
}

.loader-container {
  width: 250px;
  height: 160px;
  position: relative;
  text-align: center;
}

.text-section {
  text-align: center;
  font-size: 15px;
  color: #939393;
  /* #f65c00; */
  font-weight: bold;
  font-family: serif;
}

.g-btn {
  color: #fff;
  background-color: #fd6400;
  border-color: #fd6400;
}

td.mat-cell:last-of-type,
td.mat-footer-cell:last-of-type,
th.mat-header-cell:last-of-type {
  text-align: unset !important;
}

.completed {
  border-left: 5px solid #0eab1c !important;
}

.pending {
  border-left: 5px solid #969696 !important;
}

.cancelled {
  border-left: 5px solid red !important;
}

.title-container {
  margin: 0;
  padding: 0;
  text-align: center;
  margin-top: 25px;
}

::ng-deep #content {
  padding: 12px !important;
  height: 100vh !important;
}

.paddingEq {
  padding: unset;
}

.result-wrapper {
  padding: unset;
}

::ng-deep mat-card.mat-card {
  margin: unset !important;
  width: 100% !important;
}

ul.status {
  float: right;
  list-style: none;
  position: absolute;
  right: 25px;
  top: 20px;
}

ul.status li {
  padding-left: 0;
  display: block;
}

ul.status li:first {
  margin-bottom: 20px;
}

ul.status span {
  padding-left: 0;
  float: left;
  font-weight: bold;
}

ul.status span.text {
  vertical-align: text-bottom;
}

.green-dot {
  width: 15px;
  height: 15px;
  background-color: green;
  border-radius: 50%;
  display: block;
}

.gray-dot {
  width: 15px;
  height: 15px;
  background-color: gray;
  border-radius: 50%;
  display: block;
}

.red-dot {
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  display: block;
}

.status span {
  display: inline-block;
  margin-left: 5px;
  vertical-align: text-bottom;
}

.completed {
  border-left: 5px solid #0eab1c !important;
}

.pending {
  border-left: 5px solid #969696 !important;
}

.cancelled {
  border-left: 5px solid red !important;
}


tr.mat-row.example-element-row {
  height: 60px;
  border-radius: 10px !important;
  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.19) !important;
}

::ng-deep td.mat-column-DateReceivedStr {
  border-bottom-left-radius: 8px !important;
  border-top-left-radius: 8px !important;
}

table.mat-table {
  border-collapse: separate !important;
  border-spacing: 0 1px !important;
}

.student-element-row td:nth-child(1) {
  width: 100% !important;
}

::ng-deep .student-element-row td {
  word-break: unset !important;
}

::ng-deep th.mat-header-cell:last-of-type,
td.mat-cell:last-of-type,
td.mat-footer-cell:last-of-type {
  padding: 0 !important;

}

::ng-deep .nextBtn {
  top: 0 !important;
  position: relative !important;
  right: 0 !important;
}

::ng-deep .cutomToolTip {
  right: 135px !important;
  top: 20px !important;
  bottom: unset !important;
  z-index: 11 !important;
}

::ng-deep th.mat-header-cell {
  z-index: 10 !important;
}

.zoom-box {
  transition: transform 0.3s ease-in-out;
}

.controls {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  background: #ffffffaf;
  padding: 0 5px;
  border-radius: 5px;
}

.controls a {
  display: inline-block;
  font-size: 20px;
  color: #6c757d;
  padding-left: 5px;
}



::ng-deep .mat-card tr.mat-header-row {
  position: sticky !important;
  top: -14px !important;
  z-index: 99 !important;
}

::ng-deep .cvetcss {
  text-align: center !important;
  margin-left: 25px !important;
  padding-left: 0 !important;
} 

::ng-deep .cvetcss .col-bg{
  display: block !important;
}
::ng-deep .cvetcss .note {
  width: 150px !important;
} 